@tailwind base;
@tailwind components;
@tailwind utilities;

/* Global Styles */
@layer base {
  body {
    @apply font-sans antialiased;
  }

  h1, h2, h3, h4, h5, h6 {
    @apply font-semibold;
  }
}

/* Mapbox GL JS Customizations */
.mapboxgl-popup-content {
  @apply rounded-lg shadow-lg border border-gray-200 dark:border-gray-700;
  @apply bg-white dark:bg-gray-800;
  @apply text-gray-900 dark:text-white;
  padding: 0 !important;
}

.mapboxgl-popup-tip {
  @apply border-t-white dark:border-t-gray-800;
}

.mapboxgl-ctrl-group {
  @apply rounded-lg shadow-lg;
}

.mapboxgl-ctrl button {
  @apply transition-colors;
}

/* Custom Scrollbar */
@layer utilities {
  .scrollbar-thin::-webkit-scrollbar {
    width: 6px;
    height: 6px;
  }

  .scrollbar-thin::-webkit-scrollbar-track {
    @apply bg-gray-100 dark:bg-gray-800;
  }

  .scrollbar-thin::-webkit-scrollbar-thumb {
    @apply bg-gray-300 dark:bg-gray-600 rounded-full;
  }

  .scrollbar-thin::-webkit-scrollbar-thumb:hover {
    @apply bg-gray-400 dark:bg-gray-500;
  }
}

/* Add scrollbar to scrollable containers */
.overflow-y-auto {
  @apply scrollbar-thin;
}

/* Animations */
@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

.animate-shimmer {
  animation: shimmer 2s infinite;
}

@keyframes slide-in {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.animate-slide-in {
  animation: slide-in 0.3s ease-out;
}

/* Project Marker Styles */
.project-marker {
  cursor: pointer;
  user-select: none;
}

.project-marker:hover {
  z-index: 1000;
}

/* Loading Spinner */
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.animate-spin {
  animation: spin 1s linear infinite;
}

/* Gradient Text */
.gradient-text {
  @apply bg-gradient-to-r from-primary-600 to-secondary-600 bg-clip-text text-transparent;
}

/* Card Hover Effects */
.card-hover {
  @apply transition-all duration-300 hover:shadow-lg hover:-translate-y-1;
}

/* Glass Effect */
.glass {
  @apply bg-white/80 dark:bg-gray-800/80 backdrop-blur-md;
}

/* Focus Visible */
button:focus-visible,
a:focus-visible,
input:focus-visible,
select:focus-visible,
textarea:focus-visible {
  @apply outline-none ring-2 ring-primary-500 ring-offset-2;
}

/* Dark Mode Transitions */
* {
  @apply transition-colors duration-200;
}

/* Print Styles */
@media print {
  .no-print {
    display: none !important;
  }
}

/* Accessibility */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}

/* Custom Button Styles */
@layer components {
  .btn-primary {
    @apply px-4 py-2 bg-primary-600 text-white rounded-lg hover:bg-primary-700 transition-colors font-medium;
  }

  .btn-secondary {
    @apply px-4 py-2 bg-secondary-600 text-white rounded-lg hover:bg-secondary-700 transition-colors font-medium;
  }

  .btn-outline {
    @apply px-4 py-2 border-2 border-gray-300 dark:border-gray-600 text-gray-700 dark:text-gray-300 rounded-lg hover:bg-gray-50 dark:hover:bg-gray-800 transition-colors font-medium;
  }

  .input-field {
    @apply w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent dark:bg-gray-700 dark:text-white;
  }

  .card {
    @apply bg-white dark:bg-gray-800 rounded-lg shadow-md border border-gray-200 dark:border-gray-700 p-4;
  }
}

/* Utility Classes */
.text-balance {
  text-wrap: balance;
}

/* Responsive Typography */
@layer utilities {
  .text-responsive {
    font-size: clamp(0.875rem, 2vw, 1rem);
  }

  .heading-responsive {
    font-size: clamp(1.5rem, 5vw, 2.5rem);
  }
}

/* Safe Area for Mobile Devices */
.safe-area-bottom {
  padding-bottom: env(safe-area-inset-bottom);
}

.safe-area-top {
  padding-top: env(safe-area-inset-top);
}

/* Tour Highlight Effect */
.tour-highlight {
  position: relative;
  z-index: 45 !important;
  box-shadow: 0 0 0 4px rgba(30, 64, 175, 0.4), 0 0 20px rgba(30, 64, 175, 0.3) !important;
  border-radius: 8px;
}

.dark .tour-highlight {
  box-shadow: 0 0 0 4px rgba(59, 130, 246, 0.4), 0 0 20px rgba(59, 130, 246, 0.3) !important;
}

/* Reduce motion for users who prefer it */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Focus styles for better accessibility */
.focus-visible-ring {
  @apply focus:outline-none focus-visible:ring-2 focus-visible:ring-primary-500 focus-visible:ring-offset-2;
}

/* High contrast mode support */
@media (forced-colors: active) {
  .btn-primary,
  .btn-secondary {
    border: 2px solid currentColor;
  }

  .card {
    border: 2px solid currentColor;
  }
}

/* Touch-friendly tap targets */
@layer utilities {
  .touch-target {
    min-height: 44px;
    min-width: 44px;
  }
}

/* Smooth page transitions */
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.animate-fade-in {
  animation: fade-in 0.3s ease-out;
}

@keyframes scale-in {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.animate-scale-in {
  animation: scale-in 0.2s ease-out;
}

/* Mobile-specific styles */
@media (max-width: 1023px) {
  .mapboxgl-ctrl-top-right {
    top: 60px !important;
  }

  .mapboxgl-ctrl-bottom-right,
  .mapboxgl-ctrl-bottom-left {
    bottom: 80px !important;
  }
}

/* Improved scrollbar for mobile */
@media (max-width: 768px) {
  .scrollbar-thin::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }
}

/* Loading shimmer effect */
@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

.animate-shimmer {
  animation: shimmer 1.5s infinite;
}

/* Pulse animation for loading states */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.animate-pulse {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

/* Screen reader announcement region */
.sr-announce {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
